import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';

export interface ChipCellData {
    chips: string[];
}

export interface IChipCellParams extends ICellRendererParams {
    chips: (data: any) => string[];
}

/**
 * @category Table Cell
 * @description A cell component for displaying a list of chips.
 */

@Component({
    tag: 'at-chip-list-cell',
    shadow: false,
})
export class AtChipListCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: IChipCellParams;
    @State() chips: string[] = [];

    init(params: IChipCellParams): void {
        this.params = params as IChipCellParams;
        this.chips = params.chips(params.data);
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: IChipCellParams): boolean {
        this.init(params);
        return true;
    }

    render() {
        return (
            <Host class="flex h-full items-center">
                <at-chip-list
                    chips={this.chips}
                    show_clear_all={false}
                    readonly={true}
                ></at-chip-list>
            </Host>
        );
    }
}
